            reimb_list:[],
            headers_reimb: [
                { text: 'Action', value :'action',sortable: false},
                { text: 'Tag', value:'tag',sortable: false },
                { text: 'Purchase', value:'nature',sortable:false },  
                { text: 'Invoice', value:'invoice_num',sortable:false },                                           
                { text: 'Fund Source', value:'fundsrc',sortable:false },
                { text: 'Total', value:'total',sortable:false }
            ],   
            hv_proc:false,
            proc_list:[],
            headers_proc: [
                { text: 'Action', value :'action',sortable: false},
                { text: 'Department', value:'dept_full',sortable:false },
                { text: 'Category', value:'category',sortable:false },
                { text: 'Title', value:'record_descript',sortable:false },
                { text: 'Tag', value:'proc_id',sortable: false }          
            ], 
            headers_response:[
                { text: 'Item Code', value :'item_code',sortable: false},
                { text: 'Barcode', value:'barcode',sortable:false },
                { text: 'Item Name', value:'item_eng_name',sortable:false },
                { text: 'S/N', value:'sn',sortable:false },        
            ],
            section:'',
            pdf:'',
            step:0,
            ready:false,
            uploaded_pdf:false,
            pdf_url:'',
            pdf_ready:false,
            procurement:{
                unit_cost:[],
                reimb_id:'',
                proc_id:'',
                fundsrc:[],
            },
            supplier:{
                name:'',
                supplier_contact:'',
                supplier_address:'',               
            },
            purchase:{
                type:['single'],
                purchase_date:'',
                warranty_exp:'',
                quantity:[1,1,1,1,1],
                total_cost:[],
                invoice:'',
                variance:'',
            },
            code:{
                barcode:[],
                sn:[],
                category:[''],
                sub_category:[''],
            },
            location:{
                grp_code:'',
                building_code:'',
                location_code:'All',
                sub_location_code:''
            },
            product:{
                eng_name:[],
                chi_name:[],
                eng_dp:[],
                chi_dp:[],   
                brand:[],   
                license:[],   
                ownership:[],     
            },
            action:{
                remarks:'',
                stocktake:'',
                tag:'',
                maint_details:'',
            },
            category_list:[],
            sub_category_list:[],
            grp_list:[],
            max_id:1,
        }
    },
    async created(){
        this.sub_category_list[this.item_index] = [];
        this.code.sn[this.item_index] = [];
        await this.load_filter();
        await this.check_entry();
        this.ready=true;
        document.getElementById('entry').value='';
        document.getElementById('tmp_id').value='';
    },
    async mounted(){

    },
    methods:{
        async check_entry(){
            var entry = document.getElementById('entry').value;
            var id = document.getElementById('tmp_id').value;
            if(entry=='pending_inventory'){
                await this.select_reimb(id);
            }
        },
        reset_flr(){
            this.location.location_code = 'All';
        },
        rerender(){
            this.$forceUpdate();
        },
        trigger(target){
            this.mode = target;
            document.getElementById('smbtn').click();
            this.mode = '';
        },
        append_sn(){
            var index = this.item_index;
            var i = 0;
            if(this.purchase.quantity[index]==''){
                this.purchase.quantity[index]= 1;
            }
            this.purchase.quantity[index] = parseInt(this.purchase.quantity[index]);
            var max = this.purchase.quantity[index];
            for(i=0;i<max;i++){
                if(this.code.sn[i]==undefined){
                    this.code.sn[i]=[];
                }
            }
            this.$forceUpdate();
        },  
        prev_item(){
            this.item_index--;
        },
        next_item(){
            var next = this.item_index+1;
            if(this.code.category[next]==null){       
                this.code.category[next] = '';
                this.code.sub_category[next] = '';
                this.code.sn[next] = [];
                this.sub_category_list[next] = [];
                this.purchase.type[next] = 'single';            
                this.purchase.quantity[next] = 1;
                this.procurement.fundsrc[next] = this.procurement.fundsrc[0];
                //this.procurement.unit_cost[next] = 0;
            }
            this.item_index++;
            if(this.max_id<(this.item_index+1)){
                this.max_id = this.item_index+1;
            }
        },
        add_item_grp(){
            var next = this.item_index+1;    
            if(this.code.category[next]==null){     
                this.product.eng_name[next]='';  
                this.code.category[next] = '';
                this.code.sub_category[next] = '';
                this.code.sn[next] = [];
                this.sub_category_list[next] = [];
                this.purchase.type[next] = 'single';
                this.purchase.quantity[next] = 1;               
                this.procurement.fundsrc[next] = this.procurement.fundsrc[0];
            }
            this.item_index++;
            this.max_id++;
        },
        return_pg(){
            this.$router.go(-1);
        },
        oncompleted(){
            this.$confirm('Confirm existing current page?').then(
                ()=>{
                    this.$router.push('inventory_list');
                }
            )           
        },
        async load_filter(){
            var formData = new FormData();
            formData.append('section',this.section);
            await axios.post('inventory/web/backend/filter_preload.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.category_list = response.data.category;
                            this.grp_list = response.data.grp_code;
                        }
                    }
                )
        },
        async fetch_sub_category(){
            var index = this.item_index;
            var formData = new FormData();
            formData.append('category',this.code.category[index]);
            await axios.post('inventory/web/backend/fetch_sub_category.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.sub_category_list[index] = response.data;
                            this.$forceUpdate();
                            //this.code.sub_category = 'All';
                        } else {
                            this.code.category[index] = 'All';
                            this.code.sub_category = 'All';
                            this.sub_category_list[index] = [];
                        }
                    }
                )
        },
        async search_inv_num(){
            var formData = new FormData();
            formData.append('key',this.purchase.invoice);
            await axios.post('inventory/web/backend/search_reimb.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.reimb_list = response.data;
                            this.hv_reimb = true;
                        } else {
                            this.hv_reimb = false;
                        }
                    }
                )
        },
        close_overlay(){
            this.hv_proc = false;
            this.hv_reimb = false;
            this.hv_supplier = false;
            this.zindex = 'z-index:0';
        },  
        search_records(type){
            this.hv_supplier = false;
            this.hv_reimb = false;
            this.hv_proc = false;
            if(type=='supplier'){
                this.search_supplier();
            } else if(type=='reimb'){
                this.search_reimbursement();
            } else if(type=='proc'){
                this.search_procurement();
            } else if(type='invoice'){
                this.search_inv_num();
            }
            this.zindex = 'z-index:9999!important';
        },
        addfile_pdf(event){
        var type = this.$refs.pdf_file.files[0].type;
         if(type=='application/pdf'){
           this.pdf =  this.$refs.pdf_file.files[0];
           this.pdf_url = URL.createObjectURL(this.pdf);
           this.pdf_ready = true;
           console.log(this.pdf_url);
         } else {
           this.$alert('Please upload pdf file with extension (.pdf)');
           this.$refs.pdf_file.value = '';
           this.pdf_ready = false;
         }
      },
        change_step(mode){
            console.log(this.max_id);
            var i =0;
            var ready = true;
            if(mode=='prev'){
                if(this.step>0){
                    this.step--;
                }
            } else if(mode=='next'){
                if(this.step<3){
                    if(this.step==1){
                        for(i=0;i<this.max_id;i++){
                            console.log(this.max_id);
                            if((this.product.brand[i]==null)||(this.product.brand[i]=='')||(this.product.brand[i]==undefined)){
                                ready = false;
                            }
                        }
                    } else {
                        ready = true;
                    }
                    if(ready){
                        this.step++;
                    } else {
                        this.$alert('Please fill in all info for all items.');
                    }                  
                }
            }
        },
        next_step(){
            var mode = this.mode;
            var i =0;
            var ready = true;
            if(mode=='inner_item'){
                this.add_item_grp();
            } else if(mode=='next_item'){
                this.next_item();
            } else {
                if(this.step<2){
                    if(this.step==1){
                        for(i=0;i<this.max_id;i++){
                            if((this.product.brand[i]==null)||(this.product.brand[i]=='')||(this.product.brand[i]==undefined)){
                                ready = false;
                            }
                        }
                        if(ready){
                            this.step++;
                        } else {
                            this.$alert('Please fill in all info for all items.');
                        }
                    } else if(this.purchase.purchase_date==''){
                        this.$alert('Please select purchase date');
                    } else {
                        this.step++;
                    }             
                } else if(this.step==2){
                    this.handleSubmit();
                }
            }
        },
        async handleSubmit(){
            var i = 0;
            var j = 0;
            var k = 0;
            var formData = new FormData();
            //Pg1
            formData.append('pdf',this.pdf);
            formData.append('section',this.section);
            formData.append('proc_id',this.procurement.proc_id);
            formData.append('reimb_id',this.procurement.reimb_id);
            formData.append('invoice',this.purchase.invoice);
            formData.append('supplier_name',this.supplier.name);
            formData.append('supplier_contact',this.supplier.supplier_contact);
            formData.append('supplier_address',this.supplier.supplier_address);
            formData.append('purchase_date',this.purchase.purchase_date);
            //Pg2          
            formData.append('item_set',this.max_id);
            for(i=0;i<this.max_id;i++){
                if((this.product.eng_name[i]!=undefined)&&(this.product.eng_name[i]!='')&&(this.product.eng_name[i]!=null)){
                    formData.append('type_'+i,this.purchase.type[i]);
                    formData.append('category_'+i,this.code.category[i]);
                    formData.append('subcategory_'+i,this.code.sub_category[i]);
                    formData.append('brand_'+i,this.product.brand[i]);
                    formData.append('engname_'+i,this.product.eng_name[i]);
                    formData.append('engdp_'+i,this.product.eng_dp[i]);
                    formData.append('chiname_'+i,this.product.chi_name[i]);
                    formData.append('chidp_'+i,this.product.chi_dp[i]);
                    formData.append('fundsrc_'+i,this.procurement.fundsrc[i]);
                    formData.append('quantity_'+i,this.purchase.quantity[i]);
                    if(this.purchase.type[i]=='single'){
                        formData.append('unitcost_'+i,this.procurement.unit_cost[i]);
                    } else {
                        formData.append('totalcost_'+i,this.purchase.total_cost[i]);
                    }                          
                    j = parseInt(this.purchase.quantity[i]);
                    k=0;
                    for(k=0;k<j;k++){
                        if(this.purchase.type[i]=='single'){
                            formData.append('sn_'+i+'_'+k,this.code.sn[i][k]);
                        }
                    }
                }
            }
            //Pg3
            formData.append('grp_code',this.location.grp_code);
            formData.append('building_code',this.location.building_code);
            formData.append('location_code',this.location.location_code);
            formData.append('sublocation_code',this.location.sub_location_code);
            formData.append('remarks',this.action.remarks);
            formData.append('warranty',this.purchase.warranty_exp);
            await axios.post('inventory/web/backend/insert_new_item.php',formData)
                .then(
                    (response)=>{
                        this.$alert('Successfully Submitted Record');
                        console.log(response.data);
                        this.new_item_list = response.data;
                        this.view='completed';
                        //this.$router.push('inventory_list');
                    }
                )
        },
        async search_reimbursement(){
            if(this.procurement.reimb_id!=''){
                var formData = new FormData();
                formData.append('key',this.procurement.reimb_id);
                await axios.post('inventory/web/backend/search_reimb.php',formData)
                    .then(
                        (response)=>{
                            if(response.data!='empty'){
                                this.reimb_list = response.data;
                                this.hv_reimb = true;
                            } else {
                                this.hv_reimb = false;
                            }
                        }
                    )
                this.zindex = 'z-index:9999!important';
            }
        },
        async search_procurement(){
            if(this.procurement.proc_id!=''){
                var formData = new FormData();
                formData.append('key',this.procurement.proc_id);
                await axios.post('inventory/web/backend/search_procurement.php',formData)
                    .then(
                        (response)=>{
                            if(response.data!='empty'){
                                this.proc_list = response.data;
                                this.hv_proc = true;
                            } else {
                                this.hv_proc = false;
                            }
                        }
                    )
                this.zindex = 'z-index:9999!important';
            }
        },
        async search_supplier(){
            if(this.supplier.name!=''){
                var formData = new FormData();
                formData.append('key',this.supplier.name);
                await axios.post('inventory/web/backend/search_supplier.php',formData)
                    .then(
                        (response)=>{
                            if(response.data!='empty'){
                                this.supplier_list = response.data;
                                this.hv_supplier = true;
                            } else {
                                this.hv_supplier = false;
                            }
                        }
                    )
                this.zindex = 'z-index:9999!important';
            }
        },
        select_sup(name,loc,contact){
            this.supplier.name = name;
            this.supplier.supplier_contact = contact;
            this.supplier.supplier_address = loc;
            this.zindex = 'z-index:0';
            this.hv_supplier = false;
        },
        async select_reimb(id){
            var formData = new FormData();
            formData.append('id',id);
            await axios.post('inventory/web/backend/select_reimb.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            var i=0;
                            this.pdf_url = response.data[0].pdf[0];
                            this.procurement.reimb_id = response.data[0].tag;
                            this.procurement.proc_id = response.data[0].proc_id;
                            this.purchase.invoice = response.data[0].invoice_num;
                            for(i=0;i<response.data.length;i++){
                                this.purchase.total_cost[i] = parseFloat(response.data[i].amount); 
                                this.procurement.unit_cost[i] = parseFloat(response.data[i].amount)/parseInt(this.purchase.quantity[i]);
                                this.product.eng_name[i] = response.data[i].nature;
                                this.product.eng_dp[i] = response.data[i].reason;
                                this.procurement.fundsrc[i] = response.data[i].fundsrc;
                                if(response.data[0].receiver_type=='company'){
                                    this.supplier.name = response.data[0].receiver_name;
                                    this.supplier.supplier_address = response.data[0].address;
                                    this.supplier.supplier_contact = response.data[0].contact;
                                }
                                this.max_id++;
                            }
                            this.pdf_ready = true;
                        }
                    }
                )
            this.max_id--;
            this.zindex = 'z-index:0';
            this.hv_reimb = false;
        },
        async select_proc(proc_id,dept,record_descript,category){
            this.procurement.proc_id = proc_id;
            this.zindex = 'z-index:0';
            this.hv_proc = false;
            await this.proc_reimb(proc_id);
        }, 
        async proc_reimb(proc_id){
            var formData = new FormData();
            formData.append('proc_id',proc_id);
            await axios.post('inventory/web/backend/search_proc_reimb.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.reimb_list = response.data;
                            this.hv_reimb = true;
                        } else {
                            this.hv_reimb = false;
                        }
                    }
                )
            this.zindex = 'z-index:9999!important';
        },
    }
}
</script>
<style scoped>
.px0{
    padding-left:0;
    padding-right:0;
}
.flex{
    display:flex;
    flex-direction: row;
}
.framewrapper{
    height:77.25vh!important;
    padding:0;
    width:100%;
}
.scroll_frame{
    height:77vh!important;
    overflow-y:hidden!important;
    width:100%;
}
.ios_frame{
    height:76.5vh!important;
    overflow-y:hidden!important;
    padding:0;
}
.pdf{
    height:70vh!important;
}
.overlay_bar{
    width:100%;
    height:1.5rem;
    background-color:rgb(238,28,37);
    color:#ffffff;
    font-weight: bold;
    font-size:1rem;
    padding:0.25rem auto 0.25rem 0.5rem;
}
label{
    padding-top:0.35rem;
}
.indicator{
    padding-top:0.6rem;
    margin-right:1rem;
    color:rgb(238,28,37);
    margin-left:auto;
}
.col-12.flex{
    margin-bottom:0.45rem;
}
.col-12.flex:last-child{
    margin-bottom:0;
}
.inner_overlay{
    position:relative;
    margin-top:0;
    width:80%;
    margin-left:10%;
    margin-bottom:5%;
}
.cross_box{
    position:absolute;
    right:0;
    top:0;
}
.fa-window-close{
    transition: all 0.15s ease-in-out;
    color:rgba(255, 255, 255, 0.55);
}
.fa-window-close:hover{
    color:rgba(255, 255, 255, 1);
}
.item_wrapper{
    height:70vh!important;
}
.search_table{
    position:absolute;
    bottom:5%;
}
.item_indicator{
    font-weight: 400;
    padding-left:0.3rem;
    padding-right:0.3rem;
    margin-left:auto;
    color:rgb(26,115,232);
    cursor: pointer;
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
}
.item_indicator:hover{
    background-color:rgb(26,115,232);
    color:rgba(255,255,255,0.8);
}
.item_indicator_wrapper{
    font-weight: 400;
    padding-left:0.3rem;
    padding-right:0.3rem;
    color:rgb(26,115,232);
    margin-left:auto;
}
.direction_indicator{
    cursor:pointer;
    transition: 0.3s;
    width:1.5rem;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
}
.direction_indicator:hover{
    background-color:rgb(26,115,232);
    color:rgba(255,255,255,0.8);
}
.item_title{
    color:rgba(0,0,0,0.65);
    font-weight: 400;
    text-transform: capitalize;
}
.flex_container{
    justify-content:space-between;
    display:flex;
    flex-wrap: wrap;
    overflow-y:auto;
    max-height: 12vh;
    z-index: 100;
}
.child_flex{
    flex-grow: 1;
}
div.col-8.px0.flex_container > div.child_flex > input[type="text"].form-control{
    margin-bottom:0.4rem;
}
.left{
    margin-right:0.25rem!important;
}
.right{
    margin-left:0.25rem!important;
}
</style>


